* {
    margin: 0;
    padding: 0;
}

.search-options-wrapper {
    background-color: var(--secondary-color-lightgray);
    border-radius: 3vw;
    width: 30vw;
}

.search-options-title {
    font-size: 2rem;
    text-align: center;
}

.options-list {
    list-style: none;
    margin-top: 5vh;
}

.option-list-entry {
    line-height: 1.5rem;
    height: 1.5rem;
    width: fit-content;
    display: flex;
    margin-top: 0.2rem;
    margin-left: 0.2rem;
    transition: all 500ms;
}

.option-list-entry:hover {
    transform: scale(1.2);
}

.checkbox-label {
    text-decoration: none;
    font-size: 1.3rem;
    display: flex;
    flex-direction: row;
}

.checkbox-symbol {
    visibility: hidden;
    position: static;
    width: 1.5rem;
    padding-left: 0.5rem;
}

.checkbox-label.on > .checkbox-symbol:after {
    visibility: visible;
    position: static;
    color: #00ff58;
    padding-right: 0.5rem;
    font-weight: bold;
    content: "✓"
}

.checkbox-label.on:hover > .checkbox-symbol:after {
    color: #ff0000;
    content: "✗";
}

.checkbox-label.off:hover > .checkbox-symbol:after {
    visibility: visible;
    position: static;
    color: #00FF00;
    padding-right: 0.5rem;
    font-weight: bold;
    content: "+";
}

.option-category {
    padding-bottom: 1rem;
    padding-left: 1rem;
    font-size: 1.5rem;
}